<template>
  <div class="operation-logs">
    <box>
      <div class="search">
        <!-- 搜索栏 start -->
        <div class="search-body">
          <!-- 表单 layout="inline" 横向自适应布局 -->
          <a-form-model
            class="default-search-form"
            layout="inline"
            :model="ruleForm"
            ref="ruleForm"
            @submit="handleSubmit"
          >
            <a-form-model-item label="日志类型：">
              <a-select
                v-model="ruleForm.logsType"
                class="default-select"
                placeholder="请选择"
              >
                <a-select-option value="">
                  请选择
                </a-select-option>
                <a-select-option value="1">
                  登陆
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="选择时间：">
              <a-range-picker
                separator=""
                show-time
                v-model="ruleForm.createTime"
                dropdown-class-name="default-range-picker-dropdown"
                class="default-range-picker"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
          </a-form-model>
        </div>
        <!-- 搜索栏 end -->

        <!-- 操作按钮 start -->
        <div class="operation-btn">
          <a-button class="operation-button" type="primary" html-type="submit">
            <a-icon type="search" /> 搜索
          </a-button>
          <a-button
            class="operation-button rear-butoon"
            type="primary"
            @click="resetForm"
          >
            <a-icon type="reload" /> 重置
          </a-button>
        </div>
        <!-- 操作按钮 end -->
      </div>

      <!-- 内容区域 start -->
      <div class="content-body">
        <!-- 表格 start -->
        <a-table
          class="default-table"
          :pagination="false"
          :columns="columns"
          :data-source="datas"
          rowKey="no"
          :scroll="{ y: 'calc(90vh - 16.85vw)' }"
        >
        </a-table>
        <!-- 表格 end -->
      </div>
      <!-- 内容区域 end -->

      <!-- 分页器 start -->
      <div class="content-pagination">
        <pagination
          ref="pagination"
          :total="500"
          @change="onPaginationChange"
        />
      </div>
      <!-- 分页器 end -->
    </box>
  </div>
</template>

<script>
import Box from "../../components/Box";
import Pagination from "../../components/Pagination";

/**
 * 表头
 */
const columns = [
  {
    title: "日志类型",
    key: "logsType",
    dataIndex: "logsType"
  },
  {
    title: "日志内容",
    dataIndex: "logsContent",
    key: "logsContent"
  },
  {
    title: "操作IP",
    dataIndex: "handleIp",
    key: "handleIp"
  },
  {
    title: "操作人员",
    dataIndex: "handleUser",
    key: "handleUser"
  },
  {
    title: "浏览器",
    dataIndex: "browser",
    key: "browser"
  },
  {
    title: "操作时间",
    dataIndex: "createTime",
    key: "createTime",
    width: "14vw",
    ellipsis: true
  }
];

export default {
  components: {
    Box, // 大边框
    Pagination // 分页器
  },
  data() {
    return {
      columns: columns,
      datas: [],
      isTable: false,
      visible: false,
      pageSize: 10,
      page: 1,
      ruleForm: {
        logsType: "",
        createTime: null
      }
    };
  },
  mounted() {
    this.$refs.pagination.setData(this.page, this.pageSize);
    // setData
    this.setList();
  },

  methods: {
    /**
     * 分页时间
     */
    onPaginationChange(page, pageSize) {
      console.log(page, pageSize);
      this.pageSize = pageSize;
      this.setList();
    },

    setList() {
      let datas = [];
      for (let i = 0; i < this.pageSize; i++) {
        let item = {
          no: i + 1, // 序号
          logsType: "登录", // 日志类型
          logsContent: "登录成功", // 日志内容
          handleIp: "192.168.0.122", // 操作IP
          handleUser: "admin", // 操作人员
          browser: "Chrome", // 浏览器
          createTime: "2020-01-02 12:00:30" // 操作时间
        };
        datas.push(item);
      }
      this.datas = datas;
    },

    /**
     * 分页
     */
    onShowSizeChange() {},

    handleSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    resetForm() {
      this.$refs.ruleForm.resetFields();
      this.ruleForm = {
        logsType: "",
        createTime: null
      };
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
